import React from 'react'
import Layout from '../components/Layout/Layout'
import { useAuth } from '../components/context/Auth'

const HomePage = () => {
  const [auth,setAuth] = useAuth();
  return (
    <Layout>
       <div className="container mt-5">
        <div className="card">
          <div className="card-body">
            <h3 className="card-title">Welcome to AffordablE!</h3>
            <p className="card-text">
              We're glad to have you on board. Explore our platform and make the most out of your experience. For detailed
              information, visit your <b>Dashboard</b> for more details.
            </p>
          </div>
        </div>
      </div>
      
    </Layout>
  )
}

export default HomePage
