import React from 'react'
import Layout from '../../components/Layout/Layout'
import Usermenu from '../../components/Layout/Usermenu'
import { useAuth } from '../../components/context/Auth'
const Dashboard = () => {
  const [auth] = useAuth();
  return (
    <>
      <Layout>
      <div className='container-fluid m-4 p-4'>
        <div className='row'>
          <div className='col-md-3'>
            <Usermenu/>
          </div>
          <div className="col-md-9">
            <div className="card p-4">
              <h1 className="mb-4">Welcome, {auth.user.name}!</h1>
              <p>
                We are delighted to have you here. Hope you're having a fantastic day! If you need any assistance or have
                any questions, feel free to reach out. Enjoy exploring our platform!..Click on the <b>Know ME</b> Button on Your Screen to visit Me
             
                
              </p>
            </div>
          </div>
        </div>
      </div>
      </Layout>
    </>
  )
}

export default Dashboard
