import React from 'react'
import Adminmenu from '../../components/Layout/Adminmenu'
import Layout from '../../components/Layout/Layout'
import { useState,useEffect } from 'react'
import toast from 'react-hot-toast'
import axios from 'axios'
import axiosInstance from '../../axiosIns'
import CategoryForm from '../../components/Form/CategoryForm'
import { Modal } from 'antd'
import { Footer } from 'antd/es/layout/layout'

const Createcategory = () => {
  const [categories,setCategories] = useState([]);
  const [name,setName]=useState("")
  const [visible,setVisible] = useState(false)
  const [selected,setSelected] = useState(null)
  const [updatedName,setUpdatedName] = useState("")
  const handleSubmit= async (e)=>{
    e.preventDefault();
    try {
      console.log("Name:", name);
      const { data } = await axiosInstance.post('/api/v1/category/create-category',{
        name,
      });

      console.log(data.name)
      if(data?.success){
        toast.success(`${name} Updated Successfully`)
        getAllCategory();
      }
      else{
        toast.error('Error')
      }
    } catch (error) {
      console.log(error)
      toast.error('input form error')
    }
  }

  const getAllCategory =async()=>{
    try {
      const {data} = await axios.get('/api/v1/category/get-category')
      if(data.success){
        setCategories(data.category)
        
      }
    } catch (error) {
      console.log(error)
      toast.error('something went wrong')
    }
  }
  useEffect(()=>{
    getAllCategory()
  },[])
  const submitUpdatedName =async(e)=>{
    e.preventDefault();
    try {
       
      const {data} = await axios.put(`/api/v1/category/update-category/${selected._id}`,{name:updatedName})
      if(data.success){
        toast.success('Done')
        setSelected(null)
        setVisible(false)
        setUpdatedName("")
        getAllCategory()

      }
      else{
        toast.error('Error')
      }
    } catch (error) {
      toast.error('something went wrong')
    }
  }
  const deleteName =async(pid)=>{
   
    try {
       
      const {data} = await axios.delete(`/api/v1/category/delete/${pid}`)
      if(data.success){
        toast.success('Done')
       
        
        getAllCategory()

      }
      else{
        toast.error('Error')
      }
    } catch (error) {
      toast.error('something went wrong')
    }
  }
  return (
    <>
    <Layout>
    <div className='container-fluid m-4 p-4'>
    <div className='row'>
          <div className='col-md-3'>
            <Adminmenu/>
          </div>
          <div className='col-md-9 p-2 '>
            <h3>Manage Category</h3>
            <div className='p-3'>
              <CategoryForm handleSubmit={handleSubmit} value={name} setValue={setName}/>
            </div>
            <div>
            <table className="table">
  <thead>
    <tr>
      
      <th scope="col">Name</th>
      <th scope="col">Actions</th>
    </tr>
  </thead>
  <tbody>
    
    {categories?.map(c => (
      <>
      <tr>
      <td key={c._id}>{c.name}</td>
      <td><button className='btn btn-primary ms-2' onClick={()=>{setVisible(true); setUpdatedName(c.name);setSelected(c) }}>Edit</button><button className='btn btn-danger ms-2' onClick={()=>{deleteName(c._id)}}  >Delete</button></td>
      <td></td>
      </tr>
      
      </>
 
))}

    
 
  </tbody>
</table>

            </div>
            <Modal onCancel={()=>setVisible(false) } footer={null} visible ={visible}>
              <CategoryForm value={updatedName} setValue={setUpdatedName} handleSubmit={submitUpdatedName} />
            </Modal>
          </div>
        </div>
    </div>
   
    </Layout>
   
    </>
  )
}

export default Createcategory
