import React from 'react'
import Layout from '../components/Layout/Layout'

const PageNotFound = () => {
    const headingStyle = {
      color: 'red',           
      fontSize: '24px',        
      textAlign: 'center'      
    };
  
    return (
      <Layout>
      <h1 style={headingStyle}>404</h1>
        <h3 style={headingStyle}>Page not found</h3>
      </Layout>
    );
  };

export default PageNotFound
